.products_list {
  gap: 24px;
  max-width: 1200px;
  margin: 40px auto;
  padding: 0;
  list-style: none;
}

.products_item {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  padding: 20px;
  transition: transform .25s, box-shadow .25s;
  box-shadow: 0 2px 8px #0000000d;
}

.products_item:hover {
  transform: translateY(-6px);
  box-shadow: 0 6px 18px #0000001a;
}

.products_id {
  color: #888;
  margin-bottom: 8px;
  font-size: 14px;
}

.products_name {
  color: #2c3e50;
  margin: 0 0 10px;
  font-size: 22px;
  font-weight: 600;
}

.products_price {
  color: #27ae60;
  margin-bottom: 8px;
  font-size: 18px;
  font-weight: 500;
}

.products_description {
  color: #555;
  margin-bottom: 8px;
  font-size: 15px;
}

.products_category {
  color: #2980b9;
  margin-bottom: 6px;
  font-size: 14px;
}

.products_rating {
  color: #f39c12;
  font-size: 14px;
}
